var Bmob = require('./bmob');

var md5 = require('./utf8md5');

var sdkType = 'wechatApp';

if (typeof tt !== 'undefined') {
  sdkType = 'toutiao';
} else if (typeof qq !== 'undefined') {
  sdkType = 'qqApp';
}

var setHeader = function setHeader(config, route, method, parma) {
  var t = Math.round(new Date().getTime() / 1000);
  var rand = Bmob.utils.randomString();
  var body = method === 'get' || method === 'delete' ? '' : JSON.stringify(parma);
  var sign = md5.utf8MD5(route + t + config.securityCode + rand + body + config.serverVersion); // const sign = md5.utf8MD5(route + t + config.securityCode + rand)

  var header = {
    'content-type': 'application/json',
    'X-Bmob-SDK-Type': sdkType,
    'X-Bmob-Safe-Sign': sign,
    'X-Bmob-Safe-Timestamp': t,
    'X-Bmob-Noncestr-Key': rand,
    'X-Bmob-SDK-Version': config.serverVersion,
    'X-Bmob-Secret-Key': config.secretKey
  };

  if (config.applicationMasterKey) {
    header['X-Bmob-Master-Key'] = config.applicationMasterKey;
  }

  return header;
};

var request = function request(route) {
  var method = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'get';
  var parma = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : {};
  return new Promise(function (resolve, reject) {
    var header = setHeader(Bmob._config, route, method, parma);

    if (undefined === Bmob.User) {
      Bmob = require('./bmob');
    }

    var current = Bmob.User.current();

    if (current) {
      header['X-Bmob-Session-Token'] = current.sessionToken;
    }

    if (Bmob._config.deBug === true) {
      console.log('host:', Bmob._config.host);
      console.log('parma:', parma);
    }

    wx.request({
      url: Bmob._config.host + route,
      method: method,
      data: parma,
      header: header,
      success: function success(res) {
        if (res.data.code && res.data.error || res.data.error) {
          reject(res.data);
        }

        resolve(res.data);
      },
      fail: function fail(err) {
        console.log(err);
        reject(err);
      }
    });
  });
};

module.exports = request;